.products-wrapper {
  display: flex;
  justify-content: space-between;
}
.products-wrapper .product-item {
  width: 23%;
}
.products-wrapper .product-item img {
  width: 100%;
  height: 150px;
  padding: 2px;
  border: 1px solid #c0c0c0;
}
.products-wrapper .product-item .product-desc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}
.products-wrapper .product-item button {
  display: block;
  width: 100%;
  border: 0;
  background-color: #2a5ea5;
  color: #fff;
  padding: 7px;
  border-radius: 6px;
}

.product-info img {
  display: block;
  width: 40%;
  margin: 20px auto;
  max-height: 300px;
}
.product-info p {
  color: #2a5ea5;
  font-size: 20px;
  margin: 10px 0;
  text-align: center;
}

.icon-times {
  position: absolute;
  right: 100px;
  color: red;
  font-size: 40px;
  font-weight: 800;
  cursor: pointer;
}